@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: "Raleway", -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 100vw;
  max-height: 100vw;
  width: 100vw;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Raleway Font */
#App {
  height: 100vh;
  min-height: 100vh;
  max-height: 100vh;
  width: 100vw;
  min-width: 100vw;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  /* padding: 10vh 10vh 0 10vh; */
}

#myGithubLink, #shuffleThemeButton {
  transition: ease-in-out .12s;
}

#myGithubLink:hover, #shuffleThemeButton:hover {
  opacity: .65;
  cursor: pointer;
}

.muteButton {
  transition: ease-in-out .15s;
}

.muteButton:hover {
  cursor: pointer;
  opacity: .65;
}
#BarsVisualizer {
    flex-grow: 1;
    margin-top: 10vh;
    width: 99.9%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
}
#SettingsSelectorsContainer {
    width: 90%;
    height: 10vh;
    min-height: 10vh;
    min-height: 50px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

#SortSpeedSelector {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    margin-right: 25px;
}

#sortSpeedInput {
    width: 150px;
    outline: none;
    margin: auto 0;
}

#ArraySizeSelector {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    margin-right: 25px;
}

#arraySizeInput {
  width: 250px;
  outline: none;
  margin: auto 0;
}

.aSetttingMenuBar {
    height: 47px;
    border-radius: 30px;    
    display: flex;
}

#SortTypeSelector {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    margin-right: 25px;
}

.aSortTypeButton {
    margin: 0;
    font-weight: 600;
    font-size: 16px;
    padding: 14px 14px;
    border-radius: 25px;
    transition: ease-in-out .1s;
}

.aSortTypeButton:hover {
    -webkit-filter: hue-rotate(50);
            filter: hue-rotate(50);
    cursor: pointer;
}

#StartSortSelector {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    font-weight: 600;
    font-size: 16px;    
}

#StartSortSelector div {
    transition: ease-in-out .12s;        
}

#StartSortSelector div:hover {
    transform: translateY(-5px);
    cursor: pointer;
}
